@import "../../shared";
.step-list {
    display: flex;
    align-items: center;
    margin: 1rem 0;
    .step-separator {
        width: 100%;
        max-width: 3rem;
        border-bottom-width: 3px;
        margin: 1.25rem 0;
        &.is-active {
            border-color: $dark-teal;
        }
    }
    .step-item {
        color: $medium-grey;
        font-weight: bold;
        font-size: 1.125rem;
        margin: 0 0.5rem;
        display: flex;
        align-items: center;
        @include breakpoint($large) {
            font-size: 1.5rem;
        }
        &.is-active {
            color: $dark-teal;
            .step-index {
                border-color: $dark-teal;
                color: $orange;
            }
            .step-name {
                color: $orange;
            }
        }
        .step-index {
            text-align: center;
            border: 2px solid $medium-grey;
            border-radius: 50%;
            width: 2rem;
            height: 2rem;
            line-height: 2rem;
            margin: auto;
            display: none;
            font-size: 1rem;
            @include breakpoint($large) {
                display: block;
                width: 2rem;
                height: 2rem;
                line-height: 1.75rem;
            }
        }
        .step-name {
            font-size: 0.8rem;
            margin-left: 0.5rem;
            text-align: center;
            text-transform: uppercase;
            @include breakpoint($medium) {
                font-size: 1.125rem;
            }
        }
    }
}
